




















































































































































































































































































































































import { computed, reactive, ref, toRefs } from '@vue/composition-api';
import '@/styles/module.scss';
import * as Module from './components';

export default {
  name: 'ModuleName',

  components: {
    'module-monitor': Module.Monitor,
    'module-setup': Module.Setup,
    'module-presets': Module.Presets,
    'module-preview': Module.Default
  },
  data: () => ({
    events: [],
    input: null,
    nonce: 0
  }),

  computed: {
    timeline() {
      return this.events.slice().reverse();
    }
  },

  methods: {
    comment() {
      const time = new Date().toTimeString();
      this.events.push({
        id: this.nonce,
        text: this.input,
        time: time.replace(/:\d{2}\sGMT-\d{4}\s\((.*)\)/, (match, contents, offset) => {
          return ` ${contents
            .split(' ')
            .map(v => v.charAt(0))
            .join('')}`;
        })
      });
      this.input = null;
    }
  },

  setup() {
    // ENTER ACTIVITY NAME BELOW
    const moduleName = ref('Activity Name');
    const page = reactive({
      subpages: ['Setup', 'Presets', 'Monitor'],
      currentPage: 'Setup'
    });
    const getComponent = computed(() => {
      return `module-${page.currentPage.toLowerCase()}`;
    });
    const color = reactive({
      pilotcityColors: [
        ['#6eba80', '#3c9dcd', '#ea6764'],
        ['#eda1bf', '#fec34b', '#bdbdbd'],
        ['#ae90b0', '#f79961', '#000000']
      ],
      // ENTER ACTIVITY COLOR
      selectedColor: '#dedede'
    });
    const getColor = computed(() => {
      return color.selectedColor.substring(0, 7);
    });
    const config = ref({
      description: '',
      instruct: ['']
    });
    const menu = ref(false);
    return {
      ...toRefs(color as any),
      ...toRefs(page as any),
      config,
      moduleName,
      menu,
      getComponent,
      getColor
    };
  }
};
